@font-face {
  font-family: "nunito_sansregular";
  src: url("webfonts/nunitosans-regular-webfont.woff2") format("woff2"), url("webfonts/nunitosans-regular-webfont.woff") format("woff");
  font-weight: normal;
  font-style: normal;
}
@font-face {
  font-family: "nunito_sanssemibold";
  src: url("webfonts/nunitosans-semibold-webfont.woff2") format("woff2"), url("webfonts/nunitosans-semibold-webfont.woff") format("woff");
  font-weight: normal;
  font-style: normal;
}
.display-none {
  display: none;
}

* {
  outline: none;
  box-sizing: border-box;
}

/* elements */
body {
  -webkit-text-size-adjust: 100%;
  -moz-text-size-adjust: none;
  -ms-text-size-adjust: 100%;
  color: #484c4f;
  font-size: 14px;
  font-family: "Open Sans", sans-serif;
  font-weight: 500;
  background-color: white;
}

h2 {
  font-size: 38px;
  line-height: 100%;
  font-family: "nunito_sanssemibold";
}

h3 {
  font-size: 1.2em;
  padding: 0;
  margin: 0 0 15px 0;
  font-weight: 700;
}

h4 {
  font-size: 1em;
  font-weight: 700;
}

p {
  margin: 0;
  padding: 0 0 15px 0;
  line-height: 130%;
}
p strong {
  font-weight: 700;
}

a {
  color: #3786c1;
}

/* classes */
.col-xs-6 {
  width: 50%;
}

.big-link {
  display: inline-block;
  text-transform: uppercase;
  line-height: 100%;
  padding: 8px 12px;
  font-size: 18px;
  background: rgba(45, 45, 45, 0.6);
  border: 1px solid white;
  text-decoration: none;
  color: white;
  transition: background 500ms;
  margin: 10px 0;
  white-space: nowrap;
  font-family: "nunito_sansregular";
}
.big-link:hover {
  background: rgb(0, 0, 0);
  text-decoration: none;
  color: white;
}

.title-large {
  font-family: "Times New Roman", Times, serif;
  font-style: italic;
  color: #7a7250;
  border-bottom: 1px solid #d6d2c2;
  padding: 10px 0 15px 0;
  margin: 0 0 15px 0;
  font-weight: 400;
}
@media (max-width: 576px) {
  .title-large {
    font-size: 32px;
  }
}

.title-small {
  text-transform: uppercase;
  color: #19737b;
  font-size: 18px;
  margin: 0;
  padding: 0 0 10px 0;
}

.heading .title-large {
  text-align: center;
  border-bottom: none;
  border-top: 1px solid #353336;
  padding-top: 25px;
}

.pod {
  text-align: center;
}
.pod .text {
  padding: 0 15px;
}
.pod a {
  text-decoration: none;
  color: #484c4f;
  display: block;
}
.pod img {
  width: 100%;
  display: inline-block;
  margin-bottom: 15px;
}
.pod h2 {
  text-transform: uppercase;
  color: #19737b;
  font-size: 18px;
  margin: 0;
  padding: 0 0 6px 0;
}
@media (max-width: 768px) {
  .pod.news p {
    padding-bottom: 10px;
    border-bottom: 1px solid rgb(213.8118811881, 209.6930693069, 192.1881188119);
    margin-bottom: 15px;
  }
}
.pod .date a {
  font-weight: bold;
}

.pod a > img {
  transition: all 250ms;
}
.pod a:hover img {
  opacity: 0.6;
}

.feed-item .pod {
  text-align: left;
}
.feed-item .pod .text {
  padding: 0;
}
.feed-item .pod .text p {
  padding: 0;
}
.feed-item .location, .feed-item .date {
  font-weight: bold;
}
.feed-item .date {
  color: #3786c1;
}
.feed-item:hover .date {
  color: #0056b3;
}

table th {
  text-transform: uppercase;
}
table th, table td {
  white-space: nowrap;
  padding-right: 30px !important;
  font-size: 14px;
  text-align: center;
  border-right: 1px solid white;
}
table th:first-child, table td:first-child {
  text-align: left;
}
@media (max-width: 576px) {
  table th, table td {
    padding: 0.3em 20px 0.3em 0.3em !important;
  }
}
table td span {
  display: inline-block;
  text-indent: -9000px;
  background-color: #e7802c;
  width: 12px;
  height: 12px;
  border-radius: 50%;
  margin-top: 3px;
}

#nav-main {
  font-family: "nunito_sansregular" !important;
}

.divider {
  margin: 0;
  border-top: 1px solid #d6d2c2;
}

.filter-list {
  margin: 0 0 15px 0;
  padding: 0;
  list-style: none;
}
.filter-list .active {
  color: #353336;
  cursor: default;
}
.filter-list .active:hover {
  text-decoration: none;
}

/* bootstrap overrides */
.table thead th,
.table td, .table th {
  border-color: white !important;
}

.table.table-striped tbody tr:nth-of-type(odd) {
  background-color: #f1f1ed !important;
}

.form-control,
.btn {
  border-radius: 1px !important;
}

img.responsive {
  width: 100%;
  margin-bottom: 15px;
}

.container .heading {
  margin-top: 30px;
}

ol, ul {
  margin-left: 0;
  padding-left: 20px;
}

@media (max-width: 768px) {
  .container {
    max-width: 100% !important;
  }
}
/* ================================================================== */
.shaker {
  font-size: 11px;
  text-align: center;
  color: #bebebe;
  margin: 0 auto;
  background-color: black;
  padding: 5px 0;
  transition: all 250ms;
}
.shaker a {
  background: url("../images/sugarshaker.png") no-repeat center left;
  height: 16px;
  margin: 0;
  padding: 5px 0px 5px 21px;
  color: #bebebe;
  font-size: 11px;
  font-weight: normal;
  transition: all 500ms;
}
.shaker:hover {
  color: whitesmoke;
}
.shaker:hover a {
  color: whitesmoke;
  text-decoration: underline;
}

.font-two {
  font-family: "Times New Roman", Times, serif;
  font-style: italic;
}

/* show/hide css */
.sub-section .content {
  display: none;
  height: 0;
  overflow: hidden;
  transition: height 350ms ease-in-out;
  padding-left: 10px;
}
.sub-section .content.is-visible {
  display: block;
  height: auto;
}

/* cms content */
.cms-content img {
  max-width: 100%;
  display: block;
  margin: 0 auto 15px auto;
}
@media (min-width: 768px) {
  .cms-content img {
    max-width: 32%;
    float: left;
    margin-right: 20px;
    margin-bottom: 20px;
  }
  .cms-content img.image-right {
    float: right;
    margin-left: 20px;
    margin-bottom: 20px;
  }
}

/* coloured navigation */
.mm-panels .btn-clr-1 + .mm-listitem__btn {
  background: #96A92B;
  color: white;
}
.mm-panels .btn-clr-2 + .mm-listitem__btn {
  background: #BCC140;
  color: white;
}
.mm-panels .btn-clr-3 + .mm-listitem__btn {
  background: #DA8A4A;
  color: white;
}
.mm-panels .btn-clr-4 + .mm-listitem__btn {
  background: #A37301;
  color: white;
}
.mm-panels .btn-clr-5 + .mm-listitem__btn {
  background: #916083;
  color: white;
}
.mm-panels .btn-clr-6 + .mm-listitem__btn {
  background: #CC2D46;
  color: white;
}

