    color: white;
  }
  form .form-group textarea::-moz-placeholder, form .form-group input::-moz-placeholder { /* Firefox 19+ */
    color: white;
  }
  form .form-group textarea:-ms-input-placeholder, form .form-group input:-ms-input-placeholder { /* IE 10+ */
    color: white;
  }
  form .form-group textarea:-moz-placeholder, form .form-group input:-moz-placeholder { /* Firefox 18- */
